<template>
  <div>
    <el-row :gutter="20" class="bas-white-bg">
      <el-col :span="24" class="bas-fav-info">
        <div class="bas-card__header">
          <div class="bas-card__header-title">
            {{ $t("l.walletFav") }}
          </div>
        </div>
        <div class="bas-gray-split" />

        <div v-loading="ctrl.loading" class="bas-card__body bas-border-none">
          <div class="not-taken">
            <h5 class="not-taken">{{ $t("l.notTaken") }}</h5>
            <el-table
              type="index"
              ref="multipleTableRef"
              :data="favourTableData"
              style="width: 100%"
              @selection-change="handleSelectionChange"
              @cell-click="gotoRegist"
            >
              <el-table-column type="selection" width="55" />
              <el-table-column
                header-align="left"
                align="left"
                :class-name="['column-domain']"
                label="Domain"
                min-width="180"
                show-overflow-tooltip
              >
                <template #default="scope">{{ scope.row.domain }}</template>
              </el-table-column>
              <el-table-column
                header-align="left"
                align="left"
                property="type"
                :formatter="translateType"
                label="Type"
                width="200"
              />
              <el-table-column
                header-align="left"
                align="left"
                property="price"
                label="Price"
                width="60"
              />
              <el-table-column
                header-align="center"
                align="center"
                property="duration"
                label="Duration"
                show-overflow-tooltip
                min-width="240"
              >
                <template slot-scope="scope">
                  <span>Year:</span>
                  <el-input-number
                    v-model="scope.row.duration"
                    :precision="2"
                    :step="1.0"
                    controls-position=""
                    @change="customedDurationChanged"
                    :min="1"
                    :max="ruleState.maxRegYears"
                  >
                  </el-input-number>
                </template>
              </el-table-column>
              <el-table-column header-align="left" align="left" label="Total">
                <template #default="scope">{{ scope.row.total }}</template>
              </el-table-column>
              <el-table-column
                header-align="left"
                align="left"
                label="Favourite"
                min-width="100"
              >
                <template #default="scope">
                  <img
                    style="cursor: pointer"
                    @click="deFavour(scope.row)"
                    :src="
                      scope.row.isFavor
                        ? '/static/icons/favour.svg'
                        : '/static/icons/unfavour.svg'
                    "
                    alt=""
                  />
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
      </el-col>
    </el-row>

    <!-- regis footer -->
    <el-row :gutter="20">
      <el-col :span="24" class="text-center regist-footer">
        <div class="total">
          <span class="" style="color: #fff">{{ $t("l.Total") }}:</span>
          <h2 class="d-inline bas-text-green">{{ getTotal }}</h2>
          <span class="bas-text-green">UDID</span>
        </div>
        <div class="bas-card__footer">
          <button class="btn bas-btn-primary" @click="batchRegist">
            {{ $t("l.RegistBtn") }}
          </button>
        </div>
      </el-col>
    </el-row>

    <!-- taken list -->
    <el-row :gutter="20" style="margin-top: 50px" class="bas-white-bg">
      <el-col :span="24" class="bas-fav-info">
        <div v-loading="ctrl.loading" class="bas-card__body bas-border-none">
          <div class="not-taken">
            <h5 class="not-taken">{{ $t("l.taken") }}</h5>
            <el-table
              ref="multipleTableRef"
              :data="takenFavourTable"
              style="width: 100%"
              @selection-change="handleSelectionChange"
            >
              <!-- <el-table-column type="selection" width="55" /> -->
              <el-table-column
                header-align="left"
                align="left"
                :class-name="['column-domain']"
                label="Domain"
                min-width="180"
                show-overflow-tooltip
              >
                <template #default="scope">{{ scope.row.domain }}</template>
              </el-table-column>
              <el-table-column
                header-align="left"
                align="left"
                property="type"
                :formatter="translateType"
                label="Type"
                width="200"
              />
              <el-table-column
                header-align="left"
                align="left"
                property="owner"
                label="Owner"
                min-width="300"
              />
              <el-table-column
                property="expire"
                label="Expire"
                show-overflow-tooltip
                min-width="150"
              >
                <template slot-scope="scope">
                  <span>Exp:</span>
                  {{ scope.row.expire }}
                </template>
              </el-table-column>

              <el-table-column
                header-align="left"
                align="left"
                label="Favourite"
                min-width="100"
              >
                <template #default="scope">
                  <img
                    style="cursor: pointer"
                    @click="deTakenFavour(scope.row)"
                    :src="
                      scope.row.isFavor
                        ? '/static/icons/favour.svg'
                        : '/static/icons/unfavour.svg'
                    "
                    alt=""
                  />
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {
  wei2Bas,
  bas2Wei,
  transBAS2Wei,
  dateFormat,
  getTopFromSub,
  getSubFromSub,
  handleDomain,
  toUnicodeDomain,
  diffBnFloat,
  numThousandsFormat,
} from "@/utils";
import {
  MAX_BILLON_VOL,
  getDomainType,
  isTop,
  isSub,
  isRareTop,
  CheckLegal,
  domainSplit,
  MAX_AROOT_LEN,
} from "@/utils/Validator.js";

//import DomainProxy from '@/proxies/DomainProxy.js'
import { globalWebState } from "@/web3-lib";

import { findDomainInfo, hasTaken } from "@/web3-lib/apis/domain-api.js";
import {
  findDomain4Search,
  valid4CustomizeRoot,
} from "@/web3-lib/apis/view-api";
export default {
  name: "Favourite",
  components: {},
  computed: {
    domainType() {
      return this.domain === ""
        ? ""
        : this.$t(`g.${getDomainType(this.domain)}`);
    },

    ...Vuex.mapGetters({
      ruleState: "dapp/ruleState",
      minsubBas: (state) => parseFloat(wei2Bas(state.dapp.subGas)),
      maxYears: (state) => state.dapp.maxRegYears,
    }),
    ...Vuex.mapState({
      dappState: (state) => state.dapp,
    }),
    getTotal() {
      let total = 0;
      for (let i of this.favourPriceTable) {
        total += i.price * i.duration;

        // total += this.favourTableData[i].total;
      }
      return total;
    },
  },
  data() {
    return {
      topText: "",
      subText: "",
      domain: "",
      years: 1,
      openApplied: true,
      isCustomed: false,
      subUnitPrice: 4,
      unitPrice: 4,
      maxPriceBas: MAX_BILLON_VOL,
      topasset: {
        name: "",
        owner: "",
        openApplied: false,
        isCustomed: false,
        customPrice: 4 * 10 ** 18,
      },
      subasset: {
        name: "",
        owner: "",
      },
      ctrl: {
        loading: false,
      },
      favourTableData: [],
      favourPriceTable: [],
      takenFavourTable: [],
    };
  },
  methods: {
    formatExpireDate(exp) {
      return dateFormat(exp, "YYYY-MM-DD HH:mm:ss");
    },
    gotoRegist(row, column, cell) {
      if (!row.domain || column.label !== "Domain") return;
      if (this.$store.getters["metaMaskDisabled"]) {
        this.$metamask();
        return;
      }
      if (isTop(row.domain)) {
        this.$router.push({
          name: "domain.applydomain",
          params: {
            domainText: row.domain,
          },
        });
      } else {
        if (!getTopFromSub(row.domain) || !getSubFromSub(row.domain)) {
          return;
        }
        this.$router.push({
          path: `/domain/applysub/${getTopFromSub(row.domain)}/${getSubFromSub(
            row.domain
          )}`,
        });
      }
      // row.name = row.name.trim().toLowerCase();
      // this.$router.push({
      //   path: `/domain/detail/${row.name}`,
      // });
    },
    handleSelectionChange(val) {
      this.favourPriceTable = val;
      console.log(this.favourPriceTable);
    },
    getFavourItems() {
      let favourList = JSON.parse(localStorage.favourList);
      const web3State = this.$store.getters["web3State"];
      for (let domain of favourList) {
        findDomain4Search(domain, web3State.chainId)
          .then((resp) => {
            if (resp.state) {
              let takenFavourItem = {
                owner: "",
                expire: "",
                isFavor: true,
              };
              takenFavourItem.owner = resp.assetinfo.owner;
              takenFavourItem.expire = this.formatExpireDate(
                resp.assetinfo.expire
              );
              takenFavourItem.domain = domain;
              this.takenFavourTable.push(takenFavourItem);
            } else {
              let favourItem = {
                price: "",
                duration: 1,
                isFavor: true,
              };
              favourItem.domain = domain;
              favourItem.type = this.$t(`g.${getDomainType(domain)}`);

              if (getDomainType(domain) === "subdomain") {
                this.loadSubasset(favourItem);
              } else {
                this.loadTopasset(favourItem);
              }
              this.favourTableData.push(favourItem);
              for (let j in this.favourTableData) {
                this.favourTableData[j].total =
                  this.favourTableData[j].price *
                  this.favourTableData[j].duration;
              }
            }
          })
          .catch((ex) => {
            console.error(ex);
          });
      }
    },
    customedDurationChanged(val) {
      for (let j in this.favourTableData) {
        this.favourTableData[j].total =
          this.favourTableData[j].price * this.favourTableData[j].duration;
      }
    },
    async deFavour(row) {
      await this.$store.dispatch("favourite/deFavour", row.domain);
      this.favourTableData.remove(row);
    },
    async deTakenFavour(row) {
      await this.$store.dispatch("favourite/deFavour", row.domain);
      this.takenFavourTable.remove(row);
    },
    translateType(row) {
      let domainType = getDomainType(row.domain);
      return this.$t(`g.${domainType}`);
    },
    setUnitPrice(item) {
      const ruleState = this.$store.getters["dapp/ruleState"];
      if (
        item.domain === undefined ||
        item.domain === "" ||
        item.domain.trim() === ""
      ) {
        item.price = ruleState.subBas;
        item.total = item.price * item.duration;
        return;
      }

      let type = getDomainType(item.domain);

      const topasset = this.topasset;
      switch (type) {
        case "subdomain":
          item.price = ruleState.subBas;
          if (topasset.owner && topasset.openApplied && topasset.isCustomed) {
            console.log(item);
            item.price = wei2Bas(topasset.customPrice);
            item.total = item.price * item.duration;
          } else {
            item.price = wei2Bas(topasset.customPrice);
            item.total = item.price * item.duration;
          }
          break;
        case "raretop":
          item.price = ruleState.rareBas;
          item.total = item.price * item.duration;
          break;
        case "commontop":
          item.price = ruleState.rootBas;
          item.total = item.price * item.duration;
          break;
        default:
          break;
      }
    },
    setSubUnitPrice(item) {
      const ruleState = this.$store.getters["dapp/ruleState"];
      const subasset = this.subasset;
      const expire = subasset.expire;
      const curTs = parseInt(new Date().getTime() / 1000);
      if (
        subasset.owner &&
        subasset.openApplied &&
        subasset.isCustomed &&
        expire - curTs > 0
      ) {
        const customWei = subasset.customPrice;
        item.price = customWei ? wei2Bas(customWei) : ruleState.subBas;
        item.total = item.price * item.duration;
      } else {
        item.price = ruleState.subBas;
        item.total = item.price * item.duration;
      }
    },
    async loadTopasset(item) {
      let that = this;
      const web3State = this.$store.getters["dapp/web3State"];
      const chainId = web3State.chainId;
      if (chainId) {
        findDomainInfo(item.domain, chainId)
          .then((resp) => {
            this.setUnitPrice(item);
            if (resp.state) {
              this.topasset = Object.assign({}, resp.assetinfo);

              this.setUnitPrice(item);
            }
          })
          .catch((e) => {
            console.log(e);
          });
      }
    },
    async loadSubasset(item) {
      const web3State = await globalWebState();
      let textArr = item.domain.split(".");
      const topText = textArr[1];
      this.topText = topText;
      this.subText = textArr[0] || "";

      this.subasset = Object.assign(this.subasset, { name: topText });
      const chainId = web3State.chainId;

      if (topText) {
        findDomainInfo(topText, chainId)
          .then((resp) => {
            if (resp.state) {
              this.subasset = Object.assign({}, resp.assetinfo);
            }
            this.setSubUnitPrice(item);
          })
          .catch((ex) => {
            console.log(ex);
            this.setSubUnitPrice(item);
          });

        // if (this.subText && topText !== undefined) {
        //   const fullText = `${this.subText}.${topText}`;
        //   this.checkSubHasTaken(fullText, chainId);
        // }
      }
    },
    resetTopAsset() {
      const defSubGas = this.$store.state.dapp.subGas;
      this.topasset = Object.assign(
        {},
        {
          name: "",
          owner: "",
          openApplied: false,
          isCustomed: false,
          customPrice: defSubGas,
        }
      );
    },
    validForm() {
      let domain = this.domain;
      let msg = "";
      try {
        CheckLegal(domain);
        if (this.topasset.owner && !this.topasset.openApplied) throw 10012;
        return true;
      } catch (ex) {
        console.log(">>>>>>", ex, typeof ex);
        switch (ex) {
          case 10000:
            msg = `${domain} ${this.$t("l.Illegal")}`;
            break;
          case 10001:
            msg = this.$t("code.10001", { max: MAX_AROOT_LEN });
            break;
          case 10002:
            msg = `${domain} ${this.$t("l.ErrorHasSpecialCharacter")}`;
            break;
          case 10003:
            msg = `${domain} 域名超过二级`;
            break;
          case 10004:
            msg = `${domain} ${this.$t("l.ErrorHasUpperCase")}`;
            break;
          case 10012:
            msg = `${domain} ${this.$t("l.ErrorHasSpecialCharacter")}`;
            break;
          default:
            msg = `${domain} ${this.$t("l.Illegal")}`;
            break;
        }
        this.$message(this.$basTip.error(msg));
        return false;
      }
    },
    batchRegist() {
      for (let regDomain of this.favourPriceTable) {
        if (regDomain.type === "Sub Domain") {
        } else {
          console.log("root");
        }
      }
    },
    async commitSubRegist() {
      if (this.$store.getters["metaMaskDisabled"]) {
        this.$metamask();
        return;
      }
      if (this.validForm()) {
        const web3State = this.$store.getters["dapp/web3State"];
        let chainId = web3State.chainId;
        let wallet = web3State.wallet;
        let subText = this.subText;
        let topText = this.topText;

        const preData = {
          isSubDomain: true,
          domainText: subText,
          topText,
          openApplied: false,
          isCustomed: false,
          customPriceWei: 0,
          costWei: 0,
          years: this.years,
          chainId,
          wallet,
        };
        let subErrMsg = "";

        try {
          this.ctrl.loading = true;
          const resp = await preCheck4Sub(
            topText,
            subText,
            this.years,
            chainId,
            wallet
          );
          const commitData = Object.assign({}, preData, {
            hash: resp.hash,
            costWei: resp.costwei,
          });

          console.log("Regist Sub Data", commitData);

          this.$router.push({
            name: "domain.applyresult",
            params: {
              commitData,
            },
          });
        } catch (ex) {
          this.ctrl.loading = false;
          let msg = "";
          switch (ex) {
            case DOMAIN_HAS_TAKEN:
            case DOMAIN_FORMAT_ILLEGAL:
              msg = this.$t(`code.${ex}`, { text: subText });
              this.$message(this.$basTip.error(msg));
              return;
            case ROOT_REGIST_CLOSE:
            case DOMAIN_TOP_EXPIRED:
              msg = this.$t(`code.${ex}`, { text: topText });
              this.$message(this.$basTip.error(msg));
              return;

            case LACK_OF_TOKEN:
            case LACK_OF_ETH:
              this.$message(this.$basTip.error(this.$t(`code.${ex}`)));
              return;

            case DOMAIN_FORMAT_ILLEGAL:
              console.error(ex);
              return;
            default:
              break;
          }

          console.error(ex);
        }
      }
    },
    async commitRegist(regdomain) {
      let domain = regDomain.domain;
      if (!this.validForm()) return;
      if (this.$store.getters["metaMaskDisabled"]) {
        this.$metamask();
        return;
      }

      const issub = isSub(domain);
      const web3State = this.$store.getters["dapp/web3State"];
      const chainId = web3State.chainId;

      const subUnitPrice = this.subUnitPrice;
      if (parseFloat(subUnitPrice) <= 0.0) {
        let priceTip = this.$t("p.SaleOnPriceUnitBasValidError", {
          begin: 0,
          end: numThousandsFormat(MAX_BILLON_VOL),
        });
        this.$message(this.$basTip.error(priceTip));
        return;
      }

      try {
        const exist = await hasTaken(domain, chainId, !issub);
        if (exist) {
          this.$message(
            this.$basTip.error(this.$t(`code.200002`, { domaintext: domain }))
          );
          return;
        }
      } catch (e) {
        console.error(e);
        return;
      }

      if (issub) {
        let domainStruct = domainSplit(domain.trim().toLowerCase());
        let subText = domainStruct.subtext;
        let topText = domainStruct.toptext;
        await this.commitSubRegisting(subText, topText);
      } else {
        //top
        await this.commitTopRegisting(domain);
      }
    },
    async commitSubRegisting(subText, topText) {
      const web3State = this.$store.getters["dapp/web3State"];

      let chainId = web3State.chainId;
      let wallet = web3State.wallet;

      const commitData = {
        isSubDomain: true,
        domainText: subText,
        topText,
        openApplied: false,
        isCustomed: false,
        customPriceWei: bas2Wei(this.subUnitPrice + ""),
        costWei: 0,
        years: this.years,
        chainId,
        wallet,
      };
      try {
        this.ctrl.loading = true;
        const resp = await preCheck4Sub(
          topText,
          subText,
          this.years,
          chainId,
          wallet
        );
        this.ctrl.loading = false;
        console.log("CommitTopData:", data);

        const data = Object.assign({}, commitData, {
          hash: resp.hash,
          costWei: resp.costwei,
        });
        this.$router.push({
          name: "domain.applyresult",
          params: {
            commitData: data,
          },
        });
      } catch (ex) {
        this.ctrl.loading = false;
        let msg = "";
        switch (ex) {
          case DOMAIN_HAS_TAKEN:
          case DOMAIN_FORMAT_ILLEGAL:
            msg = this.$t(`code.${ex}`, { text: subText });
            this.$message(this.$basTip.error(msg));
            return;
          case ROOT_REGIST_CLOSE:
          case DOMAIN_TOP_EXPIRED:
            msg = this.$t(`code.${ex}`, { text: topText });
            this.$message(this.$basTip.error(msg));
            return;

          case LACK_OF_TOKEN:
          case LACK_OF_ETH:
            this.$message(this.$basTip.error(this.$t(`code.${ex}`)));
            return;

          case DOMAIN_FORMAT_ILLEGAL:
            console.error(ex);
            return;
          default:
            break;
        }
        console.error(ex);
      }
    },
    async commitTopRegisting(text) {
      let topErrMsg = "";

      const web3State = this.$store.getters["dapp/web3State"];
      const chainId = web3State.chainId;
      const wallet = web3State.wallet;
      const costWei = bas2Wei(this.getTotal);
      const customWei = bas2Wei(this.subUnitPrice);

      console.log(chainId, web3State);

      const commitData = {
        isSubDomain: false,
        domainText: text,
        openApplied: this.openApplied,
        isCustomed: this.isCustomed,
        customPriceWei: customWei,
        years: this.years,
        chainId,
        wallet,
      };

      const inputParams = {
        domaintext: text,
        isCustomed: this.isCustomed,
        customWei: customWei,
        years: this.years,
        chainId,
        wallet,
      };

      try {
        this.ctrl.loading = true;
        const resp = await preCheck4Root(inputParams);
        this.ctrl.loading = false;
        this.$router.push({
          name: "domain.applyresult",
          params: {
            commitData: Object.assign({}, commitData, {
              costWei: resp.costwei,
              hash: resp.hash,
            }),
          },
        });
      } catch (ex) {
        this.ctrl.loading = false;
        let msg = "";
        switch (ex) {
          case DOMAIN_HAS_TAKEN:
          case DOMAIN_FORMAT_ILLEGAL:
            msg = this.$t(`code.${ex}`, { text: text });
            this.$message(this.$basTip.error(msg));
            return;
          case ROOT_REGIST_CLOSE:
          case DOMAIN_TOP_EXPIRED:
            msg = this.$t(`code.${ex}`, { text: text });
            this.$message(this.$basTip.error(msg));
            return;

          case LACK_OF_TOKEN:
          case LACK_OF_ETH:
            this.$message(this.$basTip.error(this.$t(`code.${ex}`)));
            return;

          case DOMAIN_FORMAT_ILLEGAL:
            console.error(ex);
            return;
          default:
            break;
        }
        console.error(ex);
      }
    },
  },
  created() {
    this.getFavourItems();
  },
  mounted() {
    this.domain = this.$route.params.domainText;

    //this.ruleState = Object.assign({},ruleState)

    //console.log('>rule>',this.ruleState)

    let topText = getTopFromSub(this.domain);
    if (topText) {
      this.loadTopasset(topText);
    }
  },
  watch: {
    domain: function (val, old) {
      // this.setUnitPrice();
      if (val && val !== old && isSub(val)) {
        let topText = getTopFromSub(val);
        if (topText) {
          this.loadTopasset(topText);
        } else {
          this.resetTopAsset();
        }
      }
    },
  },
};
</script>
<style>
